html,
body {
  min-height: 100vh; }

body {
  display: flex;
  flex-direction: column;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.15) 0%, rgba(0, 0, 0, 0.15) 100%), radial-gradient(at top center, rgba(255, 255, 255, 0.4) 0%, rgba(0, 0, 0, 0.4) 120%) #989898;
  background-blend-mode: multiply,multiply;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; }

main, .main-section {
  flex: 1 auto;
  display: flex;
  flex-direction: column;
  padding: 0 1rem; }

a {
  color: #1c7d7e;
  transition: color .3s ease-in; }
  a:focus, a:hover {
    color: #135454; }

svg {
  vertical-align: middle; }

.app-Container {
  max-width: 768px;
  margin: auto;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2); }

.app-Header {
  padding: .5rem 1rem;
  background-color: #2b2b2b;
  color: #fff;
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase; }

.app-Name {
  margin: 0;
  font-family: Anton, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.5rem; }

.app-Content {
  padding: 2rem; }

.app-Footer {
  padding: 1rem; }

.app-Footer_Credits {
  margin: 0;
  color: #444;
  font-size: .9rem;
  text-align: center; }

.btn-SwitchCurrencies {
  display: inline-block;
  min-height: 40px;
  min-width: 40px;
  line-height: 40px;
  margin: 0 1rem;
  padding: 0;
  background-color: #FFFFFF;
  color: #fff;
  cursor: pointer;
  border: none;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  outline: none;
  transition: all .3s ease-in-out; }
  .btn-SwitchCurrencies:focus, .btn-SwitchCurrencies:hover {
    outline: none;
    background-color: #4b4b4b;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.1); }

@media screen and (min-width: 512px) {
  .btn-SwitchCurrencies:not([aria-hidden=true]) {
    display: none; } }

.btn-SwitchCurrencies[aria-hidden=true] {
  display: none; }
  @media screen and (min-width: 512px) {
    .btn-SwitchCurrencies[aria-hidden=true] {
      display: inline-block; } }

.form-Form {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media screen and (min-width: 512px) {
    .form-Form {
      flex-direction: row; } }

.form-Group {
  display: flex;
  flex-direction: column;
  width: 100%; }

.form-Control {
  margin-bottom: 1rem;
  padding: 5px 10px;
  width: 100%;
  border-radius: 3px;
  box-shadow: none;
  border: 1px solid #ddd;
  outline: none; }

.form-Control-from:focus {
  border-color: #30cfd0;
  box-shadow: 0 0 2px #30cfd0; }

.form-Control-to:focus {
  border-color: #eec111;
  box-shadow: 0 0 2px #eec111; }

.form-Select {
  background: transparent;
  border: none; }

.form-Select-from {
  background-color: #30cfd0; }
  .form-Select-from:focus {
    box-shadow: 0 0 4px #30cfd0; }

.form-Select-to {
  background-color: #eec111; }
  .form-Select-to:focus {
    box-shadow: 0 0 4px #eec111; }

.Toast-Wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  bottom: 0;
  width: 100%; }

.Toast-hidden {
  display: none; }

.Toast {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 768px;
  margin: 0 auto;
  background-color: #fff;
  padding: 0 1rem;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6); }
  @media screen and (min-width: 768px) {
    .Toast {
      flex-direction: row; } }

.Toast_Message {
  margin: 0;
  padding: 0.5rem;
  font-size: .9rem; }

.Toast_Buttons {
  padding: 0 .5rem; }

.Toast_Button {
  margin: .3rem 0;
  font-size: .9rem;
  border: none;
  border-radius: 3px;
  box-shadow: none;
  cursor: pointer;
  transition: all .3s ease-in-out; }

.Toast_Button-refresh {
  background-color: #00c14f;
  color: #fff; }
  .Toast_Button-refresh:focus, .Toast_Button-refresh:hover {
    background-color: #00a845; }

.Toast_Button-dismiss {
  background-color: #ddd;
  color: #333; }
  .Toast_Button-dismiss:focus, .Toast_Button-dismiss:hover {
    background-color: #d0d0d0; }
